'use client';

import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { updateStudent } from '@/redux/Actions/StudentActions';
import { useRouter } from 'next/router';
import { ArrowLeft, Save } from 'lucide-react';

const UpdateStudentForm = () => {
  const dispatch = useDispatch();
  const router = useRouter();

  const student = useSelector((state) => state.student.studentDetails);
console.log("student selector ",student);
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    phone: '',
    email: '',
    gender: '',
    dob: '',
    parentCode: '',
    aadhaarCard: '',
    admissionDate: '',
    bloodGroup: '',
    allergies: '',
    medicalConditions: '',
    achievements: '',
    currentSection: '',
  });

  useEffect(() => { 
    if (student) {
      setFormData({
        firstName: student.firstName || '',
        lastName: student.lastName || '',
        phone: student.phone || '',
        email: student.email || '',
        gender: student.gender || '',
        dob: student.dob ? student.dob.substring(0, 10) : '',
        parentCode: student.parentCode || '',
        aadhaarCard: student.aadhaarCard || '',
        admissionDate: student.admissionDate ? student.admissionDate.substring(0, 10) : '',
        bloodGroup: student.bloodGroup || '',
        allergies: student.allergies || '',
        medicalConditions: student.medicalConditions || '',
        achievements: student.achievements || '',
        currentSection: student.currentSection || '',
      });
    }
  }, [student]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    try {
      dispatch(updateStudent({ id: student._id, formData }));
      router.push('/Dashboard/Student/StudentList');
    } catch (error) {
      console.error(error);
    }
  };

  const handleCancel = () => {
    router.push('/Dashboard/Student/StudentList');
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-pink-50 flex items-center justify-center p-4">
      <div className="bg-white w-full max-w-4xl p-8 rounded-2xl shadow-lg">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-blue-700">Update Student</h2>
          <button onClick={handleCancel} className="text-gray-500 hover:text-blue-600 flex items-center gap-1">
            <ArrowLeft className="w-5 h-5" />
            Back
          </button>
        </div>

        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input name="firstName" value={formData.firstName} onChange={handleChange} placeholder="First Name" className="input-style" required />
          <input name="lastName" value={formData.lastName} onChange={handleChange} placeholder="Last Name" className="input-style" required />
          <input name="email" type="email" value={formData.email} onChange={handleChange} placeholder="Email" className="input-style" required />
          <input name="phone" value={formData.phone} onChange={handleChange} placeholder="Phone" className="input-style" />
          <select name="gender" value={formData.gender} onChange={handleChange} className="input-style">
            <option value="">Select Gender</option>
            <option value="male">Male</option>
            <option value="female">Female</option>
            <option value="other">Other</option>
          </select>
          <input name="dob" type="date" value={formData.dob} onChange={handleChange} className="input-style" />
          <input name="parentCode" value={formData.parentCode} onChange={handleChange} placeholder="Parent Code" className="input-style" />
          <input name="aadhaarCard" value={formData.aadhaarCard} onChange={handleChange} placeholder="Aadhaar Card Number" className="input-style" />
          <input name="admissionDate" type="date" value={formData.admissionDate} onChange={handleChange} className="input-style" />
          <input name="bloodGroup" value={formData.bloodGroup} onChange={handleChange} placeholder="Blood Group" className="input-style" />
          <input name="allergies" value={formData.allergies} onChange={handleChange} placeholder="Allergies" className="input-style" />
          <input name="medicalConditions" value={formData.medicalConditions} onChange={handleChange} placeholder="Medical Conditions" className="input-style" />
          <input name="achievements" value={formData.achievements} onChange={handleChange} placeholder="Achievements" className="input-style" />
          <input name="currentSection" value={formData.currentSection} onChange={handleChange} placeholder="Current Section" className="input-style" />

          <div className="col-span-1 md:col-span-2 flex justify-end gap-4 mt-4">
            <button type="button" onClick={handleCancel} className="bg-gray-300 hover:bg-gray-400 text-gray-800 px-4 py-2 rounded-lg">
              Cancel
            </button>
            <button type="submit" className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg flex items-center gap-2">
              <Save className="w-4 h-4" />
              Update
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default UpdateStudentForm;
